.overlay-modal {
  height: 100%;
  width: 100%;
  background: rgba(49, 49, 49, 0.945);
  position: absolute;
  z-index: 999999999;
}

.modal-container {
  background-color: rgb(221, 221, 221);
  height: 450px;
  width: 400px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 6px;
}

.close-button {
  background-color: rgb(202, 18, 18);
  border-radius: 50%;
  font-weight: bold;
  width: 30px;
  height: 30px;
  border-width: 0;
  position: absolute;
  cursor: pointer;
  right: -10px;
  top: -10px;
}

.modal-container-container {
  background-color: rgb(221, 221, 221);
  position: absolute;
  width: 100%;
  height: 20%;
}
.first-div {
  top: 15%;
  height: 27%;
}
.second-div {
  top: 42%;
  width: 50%;
  height: 16%;
}
.third-div {
  top: 58%;
  height: 41%;
}
.fourth-div {
  top: 42%;
  width: 50%;
  left: 50%;
  height: 16%;
}

.modal-title {
  background-color: #dd8431;
  border-radius: 6px;
  height: 15%;
}

.modal-title h1 {
  text-shadow: 2px 2px white;
  font-size: 2.5rem;
  z-index: 999;
  position: absolute;
  color: black;
  transform: translate(-50%, 0%);
  width: 64%;
  left: 50%;
  top: 8px;
  font-style: italic;
}

.container-p-picture {
  top: 16%;
  position: absolute;
  left: 5%;
  color: black;
}

.container-p-input {
  top: 1%;
  position: absolute;
  left: 5%;
  color: black;
}

.container-p-picture-second-div {
  left: 10%;
}
.container-p-picture-fourth-div {
  left: 10%;
}
.container-input {
  top: 52%;
  position: absolute;
  left: 5%;
  color: black;
  border-radius: 5px;
  border-width: 0px;
  box-shadow: 2px 2px black;
  outline-width: 0;
  padding-left: 5px;
}

.second-div-input {
  height: 20px;
  width: 80%;
  font-size: 0.9rem;
  left: 10%;
  top: 28px;
}
.third-div-input {
  height: 50%;
  width: 90%;
  font-size: 0.9rem;
  top: 28px;
}
.fourth-div-input {
  height: 20px;
  width: 80%;
  font-size: 0.9rem;
  left: 10%;
  top: 28px;
}

.modal-button {
  border-radius: 15px;
  font-weight: bold;
  width: 90px;
  height: 30px;
  border-width: 0;
  position: absolute;
  cursor: pointer;
}
.examine-button {
  border-width: 2px;
  border-color: #895e3b;
  color: #895e3b;
  border-style: double;
  top: 44%;
  width: 81px;
  left: 5%;
}
.upload-button {
  border-width: 2px;
  border-color: #895e3b;
  color: #895e3b;
  border-style: double;
  top: 44%;
  width: 81px;
  left: 27%;
}

.confirm-button {
  background-color: #dd8431;
  left: 50%;
  transform: translate(-50%, 0%);
  bottom: 16px;
}

::placeholder {
  font-size: 0.7rem;
}

.user-profile-container {
  position: absolute;
  width: 100px;
  height: 100px;
  right: 13%;
  top: 8%;
  position: absolute;
}

.user-picture {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border-style: solid;
  border-color: #895e3b;
  border-width: 2px;
}
